* {
  box-sizing: border-box;
  margin: 0px;
}

.filter {
  display: flex;
  flex-wrap: wrap;
}
.filter h3 {
  width: 100%;
}
.filter .size {
  cursor: pointer;
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  color: #1b1a20;
  background-color: #ececec;
  border: 1px solid transparent;
  margin-right: 10px;
  margin-bottom: 10px;
}
.filter .size:hover {
  border: 1px solid black;
}
.filter .size.active {
  background: black;
  color: white;
}
.product {
  text-align: center;
  padding: 10px;
  margin-bottom: 30px;
  border: 1px solid transparent;
  cursor: pointer;
}
.product:hover {
  border: 1px solid #eee;
}
.product img {
  width: 100%;
}
.product-title {
  font-size: 1.5em;
  margin: 10px 0px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.product-price {
  font-size: 1.2em;
}
.add-cart-btn {
  display: block;
  width: 100%;
  padding: 20px;
  background: black;
  color: white;

  text-decoration: none;
  transition: all 0.5s;
}
.add-cart-btn:hover {
  color: white;
  background: #eabf00;
  text-decoration: none;
}

.cart {
  position: fixed;
  top: 0;
  right: 0;
  width: 0vw;
  background: black;
  height: 100vh;
  overflow: hidden;
  transition: all 0.3s;
  display: flex;
  flex-direction: column;
}
.cart.active {
  width: 30vw;
  overflow: initial;
}
.cart .title {
  padding: 50px;
  text-align: center;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.cart-quantity {
  background: black;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.cart-img {
  background-image: url("data:image/png;base64,");
  width: 35px;
  height: 35px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
}
.cart-img .cart-number {
  position: absolute;
  width: 18px;
  height: 18px;
  color: #0c0b10;
  font-weight: 700;
  font-size: 0.7em;
  text-align: center;
  line-height: 18px;
  border-radius: 50%;
  background-color: #eabf00;
  bottom: 0;
  right: 0;
}
.cart-items {
  overflow: auto;
}
/* width */
.cart-items::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.cart-items::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
.cart-items::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
.cart-items::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.cart-item {
  padding: 10px;
  color: #5b5a5e;
  position: relative;
  border-bottom: 1px solid #5b5a5e;
  height: 88px;
}
.cart-item img {
  width: 15%;
}
.cart-item-info {
  width: 100%;
  padding: 0 20px;
}
.cart-item-title {
  color: white;
    font-size: 16px;
    margin-bottom: 22px;
}
.cart-item-price {
  color: #eabf00;
  font-weight: 600;
  font-size: 1.2em;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;
}
.remove-cart-item {
  width: 16px;
  height: 16px;
  top: 2px;
  right: 7%;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  font-size: 22px;
}
.cart .close-cart {
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  color: white;
  position: absolute;
  top: 0px;
  left: -50px;
  background: black;
  cursor: pointer;
}
.cart .close-cart:hover {
  background: rgb(29, 26, 26);
}
.cart-quantity.outside {
  position: fixed;
  top: 0;
  right: 0;
}
.checkout-btn {
  height: 10%;
  background: #eabf00;
  width: 100%;
  color: white;
  padding: 20px;
  font-size: 1.5em;
  text-align: center;
  margin-top: auto;
}
.nam {
  display: inline-block;
}
p.cart-item-quantity.nam {
  margin-left: 66px;
}

.alertify-notifier .ajs-message.ajs-success {
  color: white ;
  font-weight: 400;
}
.alertify-notifier .ajs-message.ajs-error {
  color: white ;
  font-weight: 400;
}